const clickoutside = {
  // 初始化指令

  bind (el, binding, vnode) {
    console.log(vnode);
    function documentHandler (e) {
      // 这里判断点击的元素是否是本身，是本身，则返回
      if (el.contains(e.target)) {
        return false;
      }
      // 判断指令中是否绑定了函数
      if (binding.expression) {
        // 如果绑定了函数 则调用那个函数，此处binding.value就是handleClose方法
        binding.value(e);
      }
    }
    // 给当前元素绑定个私有变量，方便在unbind中可以解除事件监听
    el.__vueClickOutside__ = documentHandler;
    document.addEventListener('click', documentHandler);
  },
  update () { },
  unbind (el, binding) {
    // 解除事件监听
    document.removeEventListener('click', el.__vueClickOutside__);
    delete el.__vueClickOutside__;
  },
};

export default {
  directives: { clickoutside },
  data () {
    return {
      keyList: [],
      status: 0,//0 小写 1 大写 2 数字 3 符号
      lowercase: [
        ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p'],
        ['a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l'],
        ['shift', 'z', 'x', 'c', 'v', 'b', 'n', 'm', 'del'],
        ['clear', 'close', 'finish']
      ],
      uppercase: [
        ['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'],
        ['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'],
        ['shift', 'Z', 'X', 'C', 'V', 'B', 'N', 'M', 'del'],
        ['clear', 'close', 'finish']
      ],
      numbers: [
        ['1', '2', '3'],
        ['4', '5', '6', 'del2'],
        ['7', '8', '9'],
        ['clear', '0', 'finish'],
      ],
      equip: !!navigator.userAgent.toLocaleLowerCase().match(/ipad|mobile/i)//是否是移动设备
    }
  },
  props: {
    option: {
      type: Object
    }
  },
  computed: {
    showKeyboard () {
      return this.option.show
    }
  },

  mounted () {
    this.keyList = this.lowercase
  },

  methods: {
    tabHandle ({ value = '' }) {
      if (value.indexOf('tab-num') > -1) {
        this.status = 2
        this.keyList = this.numbers;
        //数字键盘数据
      } else if (value.indexOf('tab-del') > -1) {
        this.emitValue('delete')
      }
      else if (value.indexOf('tab-clear') > -1) {
        this.emitValue('clear')
      }
      else if (value.indexOf('tab-finish') > -1) {
        this.$emit('close', false)
      }
      else if (value.indexOf('tab-En') > -1) {
        this.status = 0
        this.keyList = this.lowercase
      } else if (value.indexOf('tab-shift') > -1) {
        if (this.status === 0) {
          this.status = 1
          this.keyList = this.uppercase
        }
        else {
          this.status = 0
          this.keyList = this.lowercase
        }
      }
    },

    clickKey (event) {

      if (event.type === 'click' && this.equip) return
      let value = event.srcElement.innerText
      value && value !== '123' && value !== 'clear' && value !== 'En' && value !== 'finish'
        && value !== 'del' && value !== 'shift' ? this.emitValue(value) : this.tabHandle(event.srcElement.classList)
    },

    emitValue (key) {
      this.$emit('keyVal', key)
    },

    closeModal (e) {
      if (e.target !== this.option.sourceDom) {
        // this.showKeyboard = false
        this.$emit('close', false)
      }
    },
    switchtoEn () {
      this.status = 0
      this.keyList = this.lowercase
    },
    switchtoNum () {
      this.status = 2
      this.keyList = this.numbers;
    }
  }
}
