export default {

  data () {
    return {
      isTimeoutWarning: false,
      remainingTime: 20,
      isNeedProgressBar: true,
      count: 10,
      time: 60,
      warningCount: 10,
    };
  },
  methods: {
    timeoutChange (num) {
      this.time = num;
      this.start();
    },
    timeoutWarning (remaining) {
      this.remainingTime = remaining;
      this.isTimeoutWarning = true;
    },
    resetProgress () {
      const bar = this.$refs.progressbar;
      if (bar !== undefined && this.isNeedProgressBar) {
        this.isTimeoutWarning = false;
        this.reset();
      }
    },

    confirmLogout () {
      debugger;
      this.$router.push('/');
    },

    addMouseMoveListener (type) {
      console.log(type);
      window.addEventListener(type, this.resetProgress);
    },
    addListener (type) {
      window.addEventListener(type, this.resetProgress);
    },
    start () {
      console.log('progress start');

      this.count = this.countdown;
      console.log(this.count);
      this.timer = setInterval(this.process, 1000);
    },
    process () {
      if (this.time <= 0) {
        this.$emit('progresscompleted');
        this.stop();
        return;
      }

      if (this.time <= this.warningCount) {
        this.timeoutWarning(this.time);
      }
      this.time--;
    },
    stop () {
      clearInterval(this.timer);
      this.timer = null;
      this.countdown = this.count;
    },

    reset () {
      this.stop();
      this.start();
    },

  },

  watch: {
    $route (value) {
      this.resetProgress();
    },
  },

  created () {
    this.addMouseMoveListener('click');
    this.addListener('keypress');
    this.addListener('keydown');
  },
  beforeDestroy () {
    console.log('progressbar beforeDestroy');
    this.stop();
  },
};

